.fdcp-filtered-search {

    h2{
        margin-bottom: 2rem;
    }

    .fdcp-search-box-wrapper {
        position: relative;
        max-width: 100%;
        margin-bottom: 1rem;

        .search-icon {
            position: absolute;
            top: 50%;
            left: 32px;
            transform: translateY(-50%);
            color: #A8ADB4;
            font-size: 1.2rem;
            pointer-events: none;
            z-index: 10;
        }

        input[type="search"] {
            position: relative;
            padding: 16px 32px 16px 60px;
            width: 100%;
            border-radius: 40px;
            border: 1px solid #A8ADB4;
            background: #FFF;
            transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, outline 0.15s ease-in-out;

            font-size: 1.2rem;

            &:focus {
                outline: var(--gcds-input-outline-width) solid var(--gcds-input-focus-border);
                outline-offset: var(--gcds-input-border-width);
            }
        }
    }
}

.filter-panel {
    border: 1px solid #D6D9DD;
    border-radius: 5px;
    padding: 20px 16px;

    h3 {
        font-family: var(--gcds-font-families-heading);
        font-style: normal;
        font-weight: 700;
        margin-bottom: 1.5rem;
    }

    .filter-section {
        margin-bottom: 1.5rem;

        .fdcp-collapse-button {
            border: none;
            background: transparent;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 16px;
            align-self: stretch;
            width: 100%;
            color: #000;
            font-family: var(--gcds-font-families-body);
            font-size: 17.778px;
            font-style: normal;
            font-weight: 500;
            position: relative;
            margin-bottom: 16px;
            padding: 0;

            &:after {
                content: "\f077";
                position: absolute;
                right: 0;
                font-family: "Font Awesome 6 Free";
                font-weight: 900;
            }

            &[aria-expanded="true"] {
                &::after {
                    content: "\f078"; // Chevron-up (or rotated icon)
                }
            }
        }

        .fdcp-collapse {
            display: none;
            transition: all 0.3s ease;

            &.fdcp-show {
                display: block;
            }
        }

        .filter-option {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 0.5rem;
            padding-left: 0.2rem;

            input[type="checkbox"] {
                display: none;

                + label {
                    display: flex;
                    align-items: center;
                    cursor: pointer;
                    position: relative;
                    padding-left: 1.75rem;
                    font-size: 1rem;
                    color: #000;

                    &::before {
                        content: '';
                        display: inline-block;
                        width: 1rem;
                        height: 1rem;
                        border: 2px solid #333;
                        border-radius: 0.2rem;
                        background-color: #fff;
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }

                &:checked + label::before {
                    background-color: #1d4ed8;
                    border-color: #1d4ed8;
                    background-image: url("data:image/svg+xml,%3Csvg width='12' height='10' viewBox='0 0 12 10' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5l3 3 7-7' stroke='white' stroke-width='2' fill='none'/%3E%3C/svg%3E");
                    background-repeat: no-repeat;
                    background-position: center;
                }
            }

            .filter-count {
                background-color: #D7E5F5;
                color: #000;
                padding: 0.2rem 0.5rem;
                border-radius: 0.375rem;
                font-size: 0.875rem;
                font-weight: 600;
                min-width: 1.5rem;
                text-align: center;
                width: 2rem;
            }
        }
    }
}
